.scrollTopButton {
   position: fixed;
   right: 25px;
   bottom: 25px;
   filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
   width: 50px;
   height: 50px;
   opacity: 75%;
   transition: 0.2s ease;
   z-index: 9999;
}

.scrollTopButton:hover,
.scrollTopButton:focus {
   opacity: 100%;
}
